<template>
  <div class="personalCenter">
    <Navbar></Navbar>
    <div class="personalCenterMain">
      <div class="personalCenterTop">
        <img src="../../assets/img/pic5.png" class="pic5" alt="" />
        <div class="title">{{ $t("index.text56") }}</div>

        <div class="userAddress">{{ userAddress }}</div>

        <div
          class="copyBtn jcc"
          v-clipboard:copy="userAddress"
          v-clipboard:success="fzdzhiAs"
        >
          <img src="../../assets/img/copy2.png" class="copyIcon" alt="" />{{
            $t("index.text57")
          }}
        </div>
      </div>
      <div class="personalCenterbottom">
        <div class="selling">
          <div class="usdt">USDT</div>
          <div class="usdtNum">{{ usdt }}</div>
        </div>

        <div class="tokenBox jcsb mt2r">
          <div class="tokenItem">
            <img src="../../assets/img/pic24.png" alt="" />
            <div class="tokenItemTitle">{{ $t("index.text15") }}</div>
            <div class="tokenItemNum">
              {{ hlTotal }}
            </div>
            <div class="tokenItemBtn" @click="$router.push('/purchaseRecords')">
              {{ $t("index.text117") }}
            </div>
          </div>

          <div class="tokenItem">
            <img src="../../assets/img/pic25.png" alt="" />
            <div class="tokenItemTitle">{{ $t("index.text59") }}</div>
            <div class="tokenItemNum">{{ hf }}</div>
            <div class="tokenItemBtn" @click="$router.push('/getPoints')">
              {{ $t("index.text60") }}
            </div>
          </div>
        </div>

        <div class="recommendedInfo mt2r">
          <div class="recommendedInfoTitle jcsb">
            <span> {{ $t("index.text61") }} </span>

            <span @click="$router.push('/directRewards')">
              {{ $t("index.text62") }}</span
            >
          </div>
          <div class="peopleNum">{{ referrals.length }}</div>

          <!-- <div class="teamBenefits mt2r">
            <div class="teamBenefitsLeft">
              <div class="text jcsb">
                <span> {{ $t("index.text63") }} </span>

                <span @click="$router.push('/teamRewards')">
                  {{ $t("index.text64") }}
                </span>
              </div>
              <div class="num">{{ queryTeamUsdtAmount }}USDT</div>
            </div>
          </div> -->

          <!-- <div class="tips">
            {{ $t("index.text65") }}
          </div> -->
        </div>

        <div class="orderForm">
          <div class="item mt2r" @click="$router.push('/myOrder')">
            <div class="left">
              <img src="../../assets/img/pic14.png" class="icon" alt="" />
              {{ $t("index.text66") }}
            </div>
            <van-icon name="arrow" />
          </div>

          <div class="line"></div>

          <div class="item mt2r" @click="rewardPointsPopup = true">
            <div class="left">
              <img src="../../assets/img/pic21.png" class="icon" alt="" />
              {{ $t("index.text67") }}
            </div>
            <van-icon name="arrow" />
          </div>
        </div>
      </div>
    </div>

    <van-popup v-model="rewardPointsPopup" class="rewardPointsPopup">
      <div class="content">
        <img
          src="../../assets/img/close.png"
          class="close"
          alt=""
          @click="rewardPointsPopup = false"
        />
        <div class="title">{{ $t("index.text68") }}</div>
        <div class="balance">{{ $t("index.text73") }}:{{ hf }}</div>
        <input
          class="hfNum"
          type="text"
          :placeholder="$t('index.text69')"
          v-model="hfNum"
        />

        <textarea
          class="textarea"
          type="textarea"
          :placeholder="$t('index.text70')"
          v-model.trim="transferAddress"
          rows="5"
        ></textarea>
        <div class="confirm mt2r jcc" @click="transfer">
          {{ $t("index.text10") }}
        </div>
      </div>
    </van-popup>
  </div>
</template>

<script>
export default {
  data() {
    return {
      tabsItemIndex: 0,
      rewardPointsPopup: false,
      hfNum: "",
      transferAddress: "",
      usdt: 0,
      hl: 0,
      hf: 0,
      referrals: [],
      queryTeamUsdtAmount: 0,
      hlTotal: 0
    };
  },
  activated() {
    this.getBalance();
    this.getUserInfo();
    this.getUserHl();
  },
  methods: {
    async getBalance() {
      let usdt = await this.core
        .usdtContract()
        .balanceOf(this.userAddress)
        .call();

      this.usdt = this.fromWei(usdt);

      let hl = await this.core
        .hlContract()
        .balanceOf(this.userAddress)
        .call();
      this.hl = this.fromWei(hl);

      let hf = await this.core
        .hfContract()
        .balanceOf(this.userAddress)
        .call();
      this.hf = this.fromWei(hf);
    },
    async getUserInfo() {
      this.referrals = await this.core
        .coreContract()
        .getReferrals(this.userAddress)
        .call();
    },

    async getUserHl() {
      this.hlTotal = 0;
      let buyList = await this.core
        .coreContract()
        .getUserOrders(this.userAddress)
        .call();
      buyList.orders.forEach(item => {
        this.hlTotal += Number(this.fromWei(item.HLAmount));
      });

      let referrList = await this.core
        .coreContract()
        .getReferrOrders(this.userAddress)
        .call();
      referrList.orders.forEach(item => {
        this.hlTotal += Number(this.fromWei(item.HLAmount));
      });
    },
    async transfer() {
      if (!this.hfNum) {
        this.$toast(this.$t("index.text69"));
        return;
      }

      if (!this.isAddress(this.transferAddress)) {
        this.$toast(this.$t("index.text71"));
        return;
      }

      this.$toast.loading({
        duration: 0,
        forbidClick: true,
        message: this.$t("index.text21"),
        className: "loading"
      });
      try {
        let gasPrice = await this.getGasPrice();

        let gas = await this.core
          .hfContract()
          .transfer(this.transferAddress, this.toWei(this.hfNum))
          .estimateGas();

        this.referrals = await this.core
          .hfContract()
          .transfer(this.transferAddress, this.toWei(this.hfNum))
          .send({
            from: this.userAddress,
            gas: gas + 10000,
            gasPrice: gasPrice
          });
        this.$toast(this.$t("index.text72"));
      } catch (error) {
        this.$toast.clear();
      }
    }
  }
};
</script>

<style lang="less" scoped>
.personalCenter {
  height: 100%;
  overflow: scroll;

  .personalCenterMain {
    .personalCenterTop {
      position: relative;
      padding: 2.8rem 1.6rem;
      background: linear-gradient(134deg, #d12c21 0%, #f5c96d 100%);
      .pic5 {
        position: absolute;
        right: 0;
        bottom: 0;
        width: 11rem;
        height: 7rem;
      }
      .title {
        color: #fff;
      }

      .userAddress {
        margin-top: 1rem;
        font-weight: bold;
        line-height: 3rem;
        font-size: 2rem;
        color: #fff;
      }
      .copyBtn {
        position: relative;
        z-index: 9;
        margin-top: 1.6rem;
        display: flex;
        height: 5rem;
        background: #fff;
        font-size: 1.6rem;
        border-radius: 0.4rem;
        color: #d12c21;
        font-weight: bold;
      }
      .copyIcon {
        width: 1.75rem;
        height: 1.75rem;
        margin-right: 0.5rem;
      }
    }

    .personalCenterbottom {
      padding: 2.25rem 1.6rem 4rem;

      .selling {
        padding: 1.2rem 1.4rem;
        background: url("../../assets/img/pic23.png") no-repeat;
        background-size: 100% 100%;
        border-radius: 0.4rem;
        height: 8.8rem;
        display: flex;
        flex-direction: column;
        justify-content: space-between;
        .usdt {
          color: #fff;
        }

        .usdtNum {
          font-size: 2rem;
          color: #fff;
          font-weight: bold;
        }
      }

      .tokenBox {
        .tokenItem {
          position: relative;
          padding: 1.2rem 1.4rem;
          width: 16.45rem;
          border-radius: 0.4rem;
          background: linear-gradient(134deg, #d12c21 0%, #f5c96d 100%);
          .tokenItemTitle {
            color: #fff;
          }
          div {
            position: relative;
            z-index: 9;
          }
          img {
            top: 0;
            position: absolute;
            right: 0;
            height: 100%;
          }

          .tokenItemNum {
            width: 16.45rem;
            overflow: hidden;
            white-space: nowrap;
            color: #fff;
            font-size: 2rem;
            margin-top: 1.4rem;
          }

          .tokenItemBtn {
            display: inline-block;
            margin-top: 1.4rem;
            color: #d12c21;
            border-radius: 2rem;
            background: #fff;
            padding: 0.7rem 1.4rem;
            font-size: 1.2rem;
          }
        }
      }

      .recommendedInfo {
        padding: 1.2rem 1.4rem;
        border-radius: 0.4rem;
        background: linear-gradient(134deg, #d12c21 0%, #f5c96d 100%);

        .recommendedInfoTitle {
          span {
            color: #fff;
          }
        }

        .peopleNum {
          margin-top: 1.4rem;
          color: #fff;
          font-weight: bold;
          font-size: 2rem;
        }

        .teamBenefits {
          .teamBenefitsLeft {
            .text {
              span {
                color: #fff;
              }
            }

            .num {
              margin-top: 1.4rem;
              color: #fff;
              font-size: 2rem;
              font-weight: bold;
            }
          }

          .teamBenefitsRight {
            padding: 0.7rem 3rem;
            border-radius: 2rem;
            background: #fff;
            color: #d12c21;
            font-size: 1.6rem;
          }
        }

        .tips {
          margin-top: 1.2rem;
          line-height: 2.4rem;
          color: #fff;
          font-size: 1.2rem;
        }
      }

      .orderForm {
        .item {
          display: flex;
          align-items: center;
          justify-content: space-between;

          .left {
            display: flex;
            align-items: center;
            font-size: 1.6rem;
            font-weight: bold;
            .icon {
              width: 2.5rem;
              height: 2.5rem;
              margin-right: 0.5rem;
            }
          }

          .van-icon {
            color: #cdcdcd;
            font-size: 1.6rem;
          }
        }

        .line {
          height: 0.2rem;
          background: #cccccc;
          margin: 2rem 0;
        }
      }
    }
  }

  .rewardPointsPopup {
    border-radius: 0.5rem;
    padding: 2rem;
    width: 90vw;
    background: #fcefd3;
    .content {
      position: relative;
    }
    .close {
      position: absolute;
      top: 0;
      right: 0;
      width: 2rem;
      height: 2rem;
    }
    .title {
      text-align: center;
      font-size: 1.6rem;
      font-weight: bold;
    }

    .balance {
      margin-top: 1.4rem;
      font-weight: bold;
    }

    .hfNum {
      margin: 1.4rem 0 0 0;
      width: 94%;
      height: 4rem;
      border: 0;
      padding: 0 0 0 1.6rem;
    }

    .hfNum::placeholder {
      color: #000;
    }
    .textarea {
      margin-top: 1.6rem;
      width: 94%;
      border: 0;
      padding: 1.4rem 0 0 1.6rem;
    }

    .textarea::placeholder {
      color: #000;
    }
    .confirm {
      height: 5rem;
      background: linear-gradient(134deg, #d12c21 0%, #f5c96d 100%);
      box-shadow: 2px 5px 20px 1px rgba(255, 16, 0, 0.28);
      border-radius: 0.8rem;
      font-size: 1.6rem;
      color: #fff;
      font-weight: bold;
    }
  }
}
</style>



